.signin {
  position: relative;
  display: flex;
  flex-direction: row;
  background-color: #2148C0;
  height: 100vh;
  width: 100%;
  align-items: center;
  text-align: center;
  .signin-content {
    position: absolute;
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    border: 4px solid gray;
    height: fit-content;
    text-align: center;
    left: 300px;
    gap: 20px;
    padding: 50px;
    .user-mail {
      width: 300px;
      height: 30px;
      border-radius: 4px;
    }
    .user-password {
      width: 300px;
      height: 30px;
      border-radius: 4px;
    }
    .btn-login {
      width: 300px;
      height: 30px;
      border-radius: 10px;
      background-color: #ff0789;
      box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.30);
    }
  }
}



.chatbot-widget {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 100vh;
  .history-group {
    display: flex;
    flex-direction: column;
    width: 20%;
    background-color: #202123;
    .history-list {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 8px;
      .histroy-message {
        color: #d9d9e3;
        font-size: 15px;
        text-align: left;
        padding: 7px;
      }
      .mhistory-icon {
        background-image: url('./assets/image/message.svg');
        background-position: center;
        width: 20px;
        height: 20px;
      }
    }

  }
  .message-group {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 80%;
    padding: 30px;
    background-color: #f7f7f8;
    .conversation {
      max-height: 800px;
      overflow:auto;
      width: 100%;
      .user-message {
        background-color: #f7f7f8;
        float: left;
        clear: both;
        padding: 10px;
        width: fit-content;
        text-align: left;
      }
      .bot-message {
        background-color: #f7f7f8;
        float: right;
        clear: both;
        padding: 10px;
        // display: block;
        width: fit-content;
      }
      .ref {
        float: left;
        clear: both;
      }
    }
    .input-group {
      position: absolute;
      bottom: 20px;
      left: 10%;
      height: 60px;
      width: 80%;
      display: flex;
      flex-direction: row;
      padding: 10px;
      gap: 20px;
      border: 1px solid #d9d9e3;
      border-radius: 10px;
      align-items: center;
      .send-message {
        // position: relative;
        width: 100%;
        // max-height: 200px;
        // height: 24px;
        border: 1px none white;
        background-color: #f7f7f8;
        border-radius: 20px;
        // left: 200px;
      }
      textarea {
        outline: none;
        border: none;
        font-size: 20px;
      }
      .btn-send {
        width: 35px;
        height: 35px;
        border: none;
        // height: 70px;
        border: none;
        background-image: url('./assets/image/btn-send.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-color: #f7f7f8;
      }
    }
  }
}


.auth {
  overflow: hidden;
  width: 100%;
  max-height: 100vh;


  .auth-form {
    position: absolute;
    width: 491px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    align-items: center;
    display: flex;
    padding: 48px;
    flex-direction: column;
    align-items: center;
    gap: 32px;
    
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.16) 14%,
     rgba(255, 255, 255, 0.04) 86%);
    box-shadow: 1px 2px 8px #B81FFD;
    border-radius: 4px;
    box-sizing: border-box;
    .auth-header {
      display: flex;
      flex-direction: row;
      padding: 48px;
      gap: 32px;
      .subtitle {
        // color: var(--white, #FFF);
        /* Headline / H5 */
        font-family: Open Sans;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: 36px; /* 150% */
      }
      .link {
        font-family: Public Sans;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 12px;
        border-radius: 6px;
        align-self: stretch;
        font-family: 'Public Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        background: linear-gradient(269.95deg, #e323ff 0.02%, #7517f8 97.45%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
      }
    }
    .auth-content {
      display: flex;
      flex-direction: column;
      gap: 20px;
      .email {
        width: 395px;
        height: 48px;
        border-radius: 5px;
        background: var(--grayscale-gray-9, #0F0F13);
        color: aliceblue;
      }
      .password {
        width: 395px;
        height: 48px;
        border-radius:  5px;
        background: var(--grayscale-gray-9, #0F0F13);
        color: white;
      }
      .submit {
        width: 395px;
        height: 40px;
        align-items: center;
        justify-content: center;
        padding: 10px 24px;
        background: #5F37FB;
        border-radius: 6px;
      }
    }
  }
}